import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { fetchFunction, fetchFunction2 } from "../helpers/fetchFunctions";
const initialState = {
  token: "",
  loading: false,
  error: "",
  enter: false,
};

//for network requests we use asynkThunk
export const signupUser = createAsyncThunk("signupUser", async (body) => {
  const result = await fetchFunction("/NOTE_PAD/signup", body);

  return result;
});

export const signinUser = createAsyncThunk("signinUser", async (body) => {
  const result = await fetchFunction("/NOTE_PAD/signin", body);
  // const result = await fetchFunction("/signin", body);
  return result;
});

export const sendEmail = createAsyncThunk("sendEmail", async (body) => {
  const result = await fetchFunction("/NOTE_PAD/email-send", body);
  return result;
});

export const changePass = createAsyncThunk("changePass", async (body) => {
  const result = await fetchFunction("/NOTE_PAD/changePassword", body);
  return result;
});

export const deleteUser = createAsyncThunk("deleteUser", async ({ id }) => {
  const result = await fetchFunction2("/NOTE_PAD/deleteUser/:id");
  return result;
});

const authReducer = createSlice({
  name: "user",
  initialState,
  reducers: {
    //actions
    addToken: (state, action) => {
      state.token = localStorage.getItem("token");
    },
    logout: (state, action) => {
      state.token = null;
      localStorage.removeItem("token");
    },
  },
  //actions put in extraReducers
  extraReducers: {
    [signupUser.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
      } else {
        state.error = action.payload.message;
      }
    },
    [signupUser.pending]: (state, action) => {
      state.loading = true;
    },

    [signinUser.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
      } else {
        state.error = action.payload.message;
        state.token = action.payload.token;
        localStorage.setItem("token", action.payload.token);
      }
    },

    [signinUser.pending]: (state, action) => {
      state.loading = true;
    },

    [sendEmail.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
      } else {
        state.error = action.payload.message;
      }
    },

    [sendEmail.pending]: (state, action) => {
      state.loading = true;
    },

    [changePass.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
        state.enter = false;
      } else {
        state.error = action.payload.message;
        state.enter = true;
      }
    },
    [changePass.pending]: (state, action) => {
      state.loading = true;
    },
    [deleteUser.fulfilled]: (state, action) => {
      state.loading = false;
      if (action.payload.error) {
        state.error = action.payload.error;
      } else {
        state.error = action.payload.message;
      }
    },
    [deleteUser.pending]: (state, action) => {
      state.loading = true;
    },
  },
});

export const { addToken, logout } = authReducer.actions;
export default authReducer.reducer;
