import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Form,
  Button,
  Container,
  Row,
  Col,
  Spinner,
  Alert,
} from "react-bootstrap";
import { BoxArrowInRight } from "react-bootstrap-icons";
import { changePass } from "../reducers/authReducers";
import { useNavigate } from "react-router-dom";

export default function ConfirmPass() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [pass2, setPass2] = useState("");
  const [code, setCode] = useState("");

  const { loading, error, enter } = useSelector((state) => state.user);

  return (
    <div>
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
        <path
          fill="#d13d4b"
          fill-opacity="1"
          d="M0,224L60,229.3C120,235,240,245,360,240C480,235,600,213,720,186.7C840,160,960,128,1080,128C1200,128,1320,160,1380,176L1440,192L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"
        ></path>
      </svg>
      <Container className="mt-5">
        <div>
          <Row>
            <Container>
              <Row>
                <Col xs={12} md={6} style={{ backgroundColor: "" }}>
                  {error && <Alert variant="danger">{error}</Alert>}
                  <br />
                  {loading && (
                    <div className="text-center m-5">
                      <Spinner animation="border" role="status">
                        <span className="visually-hidden">Loading...</span>
                      </Spinner>
                    </div>
                  )}

                  {/* <h5>Your OTP will be destroy in 5 Minutes</h5> */}
                  <h3>Change Password</h3>

                  <br />

                  <Form>
                    <Form.Group className="mb-3" controlId="email">
                      <Form.Label>Email</Form.Label>
                      <Form.Control
                        placeholder="Enter Your Email"
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                      />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="username">
                      <Form.Label>OTP</Form.Label>
                      <Form.Control
                        placeholder="Enter Your OTP"
                        type="text"
                        value={code}
                        onChange={(e) => setCode(e.target.value)}
                        placeholder="Enter Your username or email"
                      />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="note">
                      <Form.Label>Password</Form.Label>
                      <Form.Control
                        type="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        placeholder="password"
                      />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="note">
                      <Form.Label>confirm Password</Form.Label>
                      <Form.Control
                        type="password"
                        value={pass2}
                        onChange={(e) => setPass2(e.target.value)}
                        placeholder=" confirm password"
                      />
                    </Form.Group>

                    <Button
                      variant="danger"
                      className="mt-3"
                      onClick={() => {
                        if (password == pass2) {
                          dispatch(changePass({ email, code, password }));
                          navigate("/");
                        } else {
                          setPass2("");
                          setPassword("");
                        }
                      }}
                    >
                      <BoxArrowInRight style={{ marginRight: 3 }} />
                      submit
                    </Button>
                  </Form>
                </Col>
              </Row>
            </Container>
          </Row>
        </div>
      </Container>

      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
        <path
          fill="#d13d4b"
          fill-opacity="1"
          d="M0,32L48,37.3C96,43,192,53,288,69.3C384,85,480,107,576,133.3C672,160,768,192,864,186.7C960,181,1056,139,1152,133.3C1248,128,1344,160,1392,176L1440,192L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"
        ></path>
      </svg>
    </div>
  );
}
